body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  height: 100vh;
  background-color: #1a202c;
  overflow: scroll;
  text-align: center;
}

.container {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0 30px 0 30px;
}

.authed-container {
  height: 100%;
  display: flex;
  flex-direction: column;
  padding: 30px;
}

.header {
  margin: 0;
  font-size: 50px;
  font-weight: bold;
  color: white;
}

.sub-text {
  font-size: 25px;
  color: white;
}

.gradient-text {
  background: -webkit-linear-gradient(left, #60c657 30%, #35aee2 60%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.cta-button {
  height: 45px;
  border: 0;
  width: auto;
  padding-left: 40px;
  padding-right: 40px;
  border-radius: 10px;
  cursor: pointer;
  font-size: 16px;
  font-weight: bold;
  color: white;
}

.connect-wallet-button {
  background: -webkit-linear-gradient(left, rgb(12, 51, 178), #e235c8);
  background-size: 200% 200%;
  -webkit-animation: gradient-animation 4s ease infinite;
          animation: gradient-animation 4s ease infinite;
}

.submit-gif-button {
  background: -webkit-linear-gradient(left, #4e44ce, #35aee2);
  background-size: 200% 200%;
  -webkit-animation: gradient-animation 4s ease infinite;
          animation: gradient-animation 4s ease infinite;
  margin-left: 10px;
}

.footer-container {
  display: flex;
  justify-content: center;
  align-items: center;
  /* position: absolute; */
  width: 100%;
  bottom: 0;
  left: 0;
  padding-bottom: 45px;
}

.twitter-logo {
  width: 35px;
  height: 35px;
}

.footer-text {
  color: white;
  font-size: 16px;
  font-weight: bold;
}

.gif-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(500px, 1fr)); 
  grid-gap: 1.5rem;
  justify-items: center;
  margin: 0;
  margin-bottom: 60px;
  padding: 0;
}

.gif-grid .gif-item-container {
  display: flex;
  flex-direction: column;
  position: relative;
  justify-self: center;
  align-self: center;
}

.gif-item {
  display: flex;
  flex-direction: row;
}

.gif-item img {
  display: flex;
  width: 100%;
  height: 300px;
  border-radius: 10px;
  object-fit: cover;
  margin-top: 5px;
}

.gif-user-address {
  color: white;
}

.gif-side-container {
  display: flex;
  flex-direction: column;
  margin-left: 5px;
}

.gif-vote-container {
  display: flex;
  flex-direction: column;
  margin-bottom: auto;
}

.gif-upvote-button, .gif-downvote-button {
  background: -webkit-linear-gradient(left, #4e44ce, #35aee2);
  color: white;
  border: 0;
  width: auto;
  padding-left: 10px;
  padding-right: 10px;
  border-radius: 10px;
  cursor: pointer;
  font-size: 16px;
  font-weight: bold;
  background-size: 200% 200%;
  -webkit-animation: gradient-animation 4s ease infinite;
          animation: gradient-animation 4s ease infinite; 
}

.gif-standard-count, .gif-upvote-count, .gif-downvote-count {
  text-align: center;
  font-weight: bold;
  font-size: 20px;
}

.gif-standard-count{
  color: white;
}

.gif-upvote-count {
  color: rgb(0, 255, 8);
}

.gif-downvote-count {
  color: rgb(255, 7, 7);
}

.delete-button {
  background: rgb(225, 96, 96);
  color: white;
  border: 0;
  width: auto;
  padding-left: 10px;
  padding-right: 10px;
  border-radius: 10px;
  margin-left: 5px;
  cursor: pointer;
  font-size: 16px;
  font-weight: bold;
  background-size: 200% 200%;
  -webkit-animation: gradient-animation 4s ease infinite;
          animation: gradient-animation 4s ease infinite; 
}

.connected-container input[type='text'] {
  display: inline-block;
  color: white;
  padding: 10px;
  width: 50%;
  height: 60px;
  font-size: 16px;
  box-sizing: border-box;
  background-color: rgba(0, 0, 0, 0.25);
  border: none;
  border-radius: 10px;
  margin: 50px auto;
}

.connected-container button {
  height: 50px;
}

